import React from 'react';
import { Container, Row, Col } from 'react-bootstrap';
import { FaFacebookSquare, FaTwitterSquare,
        FaGithubSquare, FaLinkedin } from 'react-icons/fa';

function Footer() {

    return(
        <footer className="mt-5">
            <Container fluid={true} style={{ color: "#green" }}>
                <Row className="border-top justify-content-between p-3">
                    <Col className="p-0 d-flex justify-content-start" md={3} sm={12}>
                        Danny Vega || Frontend Developer
                    </Col>
                    <Col className="p-0 d-flex justify-content-start mt-3 mb-3" md={3}>
                        Follow Me: 
                        <a href="https://www.facebook.com/Danny.Fantome.Vega/" target="_blank">
                            <FaFacebookSquare className="facebook-icon" />
                        </a>
                        <a href="https://twitter.com/Danny50066959" target="_blank">
                            <FaTwitterSquare className="twitter-icon" />
                        </a>
                        <a href="https://github.com/dannyvega-studio" target="_blank">
                            <FaGithubSquare className="github-icon" />
                        </a>
                        <a href="https://www.linkedin.com/in/daniel-vega-95347a1ba/" target="_blank">
                            <FaLinkedin className="linkedin-icon" />
                        </a>
                    </Col>
                </Row>
            </Container>
        </footer>
    );
}

export default Footer;