import React from 'react';
import './App.css';
//Bootstrap//
import 'bootstrap/dist/css/bootstrap.min.css';
import {BrowserRouter as Router, Route, Link } from 'react-router-dom';
import { Container, Nav, Navbar } from 'react-bootstrap';
//Componentes//
import Footer from './components/Footer';
import WhatsApp from './components/WhatsApp';
//Paginacion//
import HomePage from './pages/HomePage';
import AboutPage from './pages/AboutPage';
import PortfolioPage from './pages/PortfolioPage';

class App extends React.Component {
    
  constructor(props) {
    super(props);
    this.state = {
      title: '</> Danny Vega Portfolio </>',
      headerLinks: [
        { title: 'Home', path: '/' },
        { title: 'About', path: '/about' },
        { title: 'Portfolio', path: '/portfolio' }
      ],
      home: {
        title: 'Be Relentless',
        subTitle: 'Projects the make a difference',
        text: 'Checkout my projects below'
      },
      about: {
        title: 'About Me',
        subTitle: 'Just me being myself',
      },
      portfolio: {
        title: 'Be Creative',
        subTitle: 'Projects the make a difference',
        text: 'Checkout my portfolio projects below'
      }
    }
  }

render() {
  return (
    <div className="App">

      <Router>
        <Container className="p-0" fluid={true}>
          
          <Navbar className="border-bottom navbar-dark bg-dark" expand="lg">
            <Navbar.Brand>Danny Vega || Frontend Developer</Navbar.Brand>

            <Navbar.Toggle className="border-0" aria-controls="navbar-toggle" />
            <Navbar.Collapse id="navbar-toggle">
              <Nav className="ml-auto">
                <Link className="nav-link" to="/">Home</Link>
                <Link className="nav-link" to="/about">About</Link>
                <Link className="nav-link" to="/portfolio">Portfolio</Link>
              </Nav>
            </Navbar.Collapse>
          </Navbar>
          
          <Route path="/" exact render={() => <HomePage title={this.state.home.title} subTitle={this.state.home.subTitle} text={this.state.home.text} />} />
          <Route path="/about" render={() => <AboutPage title={this.state.about.title} subTitle={this.state.about.subTitle} />} />
          <Route path="/portfolio" render={() => <PortfolioPage title={this.state.portfolio.title} subTitle={this.state.portfolio.subTitle} text={this.state.portfolio.text} />} />
          
          <WhatsApp />
          <Footer />
          
        </Container>
      </Router>
    
      </div>
    );
  }
}

export default App;
